import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import Delete from 'material-ui/svg-icons/action/delete';
import { database } from './firebase';

class App extends Component {
  constructor() {
    super();

    this.state = {
      messages: [],
    };

    this.onAddMessage = this.onAddMessage.bind(this);
    this.deleteMessage = this.deleteMessage.bind(this);
    this.deleteAll = this.deleteAll.bind(this);
    this.focusText = this.focusText.bind(this);
  }

  componentDidMount() {
    this.scrollToBot();
  }

  componentWillMount() {
    const messagesRef = database.ref('messages')
      .orderByKey()
      .limitToLast(100);

    messagesRef.on('child_added', snapshot => {
      const message = { text: snapshot.val(), id: snapshot.key };

      this.setState(prevState => ({
        messages: [...prevState.messages, message],
      }));
    });

  }

  componentDidUpdate() {
    this.scrollToBot();
  }

  scrollToBot() {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }

  onAddMessage(event) {
    event.preventDefault();
    if (this.input.value !== "") {
      database.ref('messages').push(this.input.value);
      this.input.value = '';
    }
  }

  deleteMessage(e) {
    const newState = this.state.messages.filter((item) => {
      return item.id !== e
    })
    this.setState({messages: newState})
    database.ref('messages').child(e).remove();
  }

  deleteAll() {
    this.setState({messages: []});
    database.ref('messages').remove();
  }

  focusText() {
    this.input.focus();
  }

  render() {
    return (
      <div className="App">
        <div className="chatroom">
          <h3>Ping's Chat App <MuiThemeProvider><Delete className="delete" onClick={this.deleteAll} /></MuiThemeProvider></h3>
          <ul className="chats" ref="chats">
            {this.state.messages.map(message =>
              <li className="chat right" key={message.id} onClick={() => this.deleteMessage(message.id)}>{message.text}</li>
            )}
            <div style={{ float: "left", clear: "both" }}
              ref={(el) => { this.messagesEnd = el; }}>
            </div>
          </ul>
          <form onSubmit={this.onAddMessage} className="input">
            <input autoFocus type="text" ref={node => this.input = node} />
            <input type="submit" onClick={this.focusText} />
          </form>
        </div>
      </div>
    );
  }
}

export default App;


